import React from 'react';
import { connect } from 'react-redux';
import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import loadingComponent from 'src/components/Icons/LoadingComponent';
import Reload from 'src/components/Widget/Reload';
import CommonStyle from '../components/CommonResources/common.css';
import * as selectors from '../reducers/selectors';
import { shouldComponentUpdateGen } from '../utils/helper';
import * as uiActions from '../actions/ui';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'ChildApp',
  [
    'visitorListShowDetails',
    'hasVisitors',
  ]
);
class ChildApp extends React.Component {
  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  render() {
    const {
      visitorListShowDetails,
      hasVisitors,
      onHideVisitorListDetailsClick,
    } = this.props;
    return (
      <div className={CommonStyle.childWindow}>
        <LazilyLoad
          modules={{
            SplitterVisitorList: () => importLazy(new Promise((resolve, reject) => {
              require.ensure(['./SplitterVisitorList'], (require) => {
                resolve(require('./SplitterVisitorList'));
              }, 'visitor').catch(reject);
            })),
          }}
          loading={loadingComponent}
          retry={Reload}
        >
          {
            ({ SplitterVisitorList }) => (
              <SplitterVisitorList
                visitorListShowDetails={visitorListShowDetails}
                hasVisitors={hasVisitors}
                onHideVisitorListDetailsClick={onHideVisitorListDetailsClick}
              />
            )
          }
        </LazilyLoad>
      </div>
    );
  }
}

ChildApp.propTypes = {
  visitorListShowDetails: React.PropTypes.bool.isRequired,
  hasVisitors: React.PropTypes.bool.isRequired,
  onHideVisitorListDetailsClick: React.PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  visitorListShowDetails: selectors.getIfShowDetailsInVisitorList(state),
  hasVisitors: (selectors.getVisitorList(state) || []).length > 0,
});

const mapDispatchToProps = dispatch => ({
  onHideVisitorListDetailsClick() {
    dispatch(uiActions.toggleVisitorDetails());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ChildApp);



