import configureStore from './store/configureChildStore';
import { initData } from './store/windowsManager';
import uiInitialize from './childWinow';
import channelCenter from './subscriptions/channelCenter';

const store = configureStore();

window.ifChildWindow = true;

uiInitialize(store);
initData(store);
channelCenter(store);
if (!process.env.IS_WEB_APP) {
  require('./subscriptions/contextMenu').default(store); // eslint-disable-line global-require
}



